.App {
  text-align: center;
}

body,
html {
  padding: 0;
  margin: 0;
  height: 100vh;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.modal {
  /* height: 22vh;
  width: 30vw; */
  background-color: #282c34;
  color: whitesmoke;
  border-radius: 1rem;
}

@media only screen and (min-width: 320px) {
  .modal {
    width: 60vw;
    height: 35vh;
  }
}

@media only screen and (min-width: 768px) {
  .modal {
    width: 40vw;
    height: 27vh;
  }
}

@media only screen and (min-width: 1268px) {
  .modal {
    width: 50vw;
    height: 22vh;
  }
}

.modal__button {
  text-decoration: none;
  color: lightblue;
  border: solid 1px whitesmoke;
  border-radius: 1rem;
  margin-top: 1rem;
  padding: 0.5rem 6%;
  text-decoration: none;
}

.modal > h1 {
  background-color: #282c34;
  margin-top: 0;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.modal > h3 {
  margin-bottom: 2rem;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.modal__button:hover {
  background-color: rgb(11, 33, 131);
  color: navy;
}

.modal__button:visited {
  color: lightblue;
}

.modal__button > a {
  color: navy;
}
