@import "../../../partials/mixins";

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 1.5rem;
  height: 1.5rem;
  left: 1.5rem;
  top: 5.3rem;

  @include tablet {
    display: none;
  }
  
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #3f4979;
  @include tablet {
    display: hidden;
  }
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #4789c0;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
  @include tablet {
    display: hidden;
  }
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  opacity: 95%;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
  color: whitesmoke;
  
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: whitesmoke;
  
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.menu-item {
  margin-bottom: 2rem;
}